  // 安全库存金额校验
  safetyStockCheck(data) {
    return new Promise(function (reslove, reject) {
      axios({
        url: "v2/risk/VerifyBills/getVerifyBills",
        method: "post",
        data: data
      })
        .then(({ data }) => {
          if (data.code === 200) {
            reslove(data);
          } else if (data.code === 362) {
            MessageBox.alert(
              "您好，您的仓库：" + data.data + "已经低于该仓库的安全库存金额！",
              "提示",
              {
                confirmButtonText: "确定",
                callback: action => { }
              }
            );
          } else if (data.code === 363) {
            MessageBox.confirm(
              "您好，您的仓库：" +
              data.data +
              "已经低于该仓库的安全库存金额！是否继续通过？",
              "提示",
              {
                distinguishCancelAndClose: true,
                confirmButtonText: "继续",
                cancelButtonText: "取消"
              }
            )
              .then(() => {
                reslove(data);
              })
              .catch(action => { });
          }
        })
        .catch(msg => {
          reslove(data);
        });
    });
  },
