// 销售订单来源
import axios from "common/utils/axios";
export const saleOrderType = {
  'ERP': [0, null],
  '线上商城': 1,
  '采购订单转单': 2,
  '拼多多': 3,
  '全部': 4
};
// 获取属性
export function get(val, obj) {
  for (var i in obj) {
    if (Array.isArray(obj[i])) {
      for (var j = 0; j < obj[i].length; j++) {
        if (obj[i][j] === val) {
          return i;
        }
      }
    } else if (val === obj[i]) {
      return i;
    }
  }
}
// 车郎中订单校验
export function checkCarDoctor(params) {
  return new Promise(function (reslove, reject) {
    axios({
      method: "post",
      url: "/v2/open/VerifyBills/verifySubscribeBills",
      data: params
    })
      .then(({
        data
      }) => {
        if (data.code == 200) {
          reslove(data.msg);
        } else {
          reject();
        }
      })
      .catch(() => {
        reslove();
      });
  });
}
export function checkComputation(arr, obj, type = 0) {
  var result = [];
  result = arr.map(val => {
    var reobj = {};
    for (var i in obj) {
      reobj[i] = val[obj[i]];
    }
    return reobj;
  });
  return new Promise(function (reslove, reject) {
    axios({
      method: "post",
      url: type ?
        "purchase_center/open/TotalPrice/checkoutGoodsTotalPrice" :
        "v2/open/TotalPrice/checkoutGoodsTotalPrice",
      data: {
        goods_json: JSON.stringify(result)
      }
    })
      .then(({
        data
      }) => {
        if (data.code == 200) {
          reslove(data.msg);
        } else {
          reject();
        }
      })
      .catch(() => {
        reject();
      });
  });
}
export function settingInfo(obj, Callback = () => { }) {
  axios({
    method: "get",
    url: "open/BillSetting/getSettingInfo",
    params: {
      type: obj.num,
      company_id: Number(obj.company_id),
      repeat: true
    }
  }).then(res => {
    if (obj.num == 3) {
      localStorage.setItem("FDBName_business", JSON.stringify(res.data.data));
      Callback();
    } else if (obj.num == 2) {
      localStorage.setItem(
        "Documents_operation",
        JSON.stringify(res.data.data)
      );
      Callback();
    } else if (obj.num == 1) {
      localStorage.setItem("hasAuditorStatusPrint", res.data.data[0].status);
      Callback();
    }
  });
}
// 集采的方法 集采环境的改变 清空 客户相关
export function fromChange(obj) {
  var box = [
    "customer_id",
    "customer_name",
    "principal_name",
    "warehouse_id",
    "warehouse_name"
  ];
  box.forEach(val => {
    if (obj[val] !== undefined && obj[val] !== null) {
      obj[val] = "";
    }
  });
}
