import axios from "common/utils/axios";
export default {
  MoneyManagementCredit(params) {
    return axios({
      url: "service_station/funds/CustomerAccount/credit",
      method: "post",
      data: params
    });
  },
  MoneyMangetRebatePriceSum(params) {
    //供应商返利金额合计
    return axios({
      url: "service_station/funds/Rebate/getRebatePriceSum",
      method: "get",
      params: params
    });
  },
  getOrderPayInfo(params) {
    //获取订单支付数据
    return axios({
      url: "/service_station/purchase/Order/getOrderPayInfo",
      method: "get",
      params: params
    });
  },
  savePaymentVoucher(params) {
    // 保存付款凭证
    return axios({
      url: "/service_station/purchase/Order/savePaymentVoucher",
      method: "post",
      data: params
    });
  },
  MoneyMangetgetRebateInfo(params) {
    //供应商返利详情
    return axios({
      url: "service_station/funds/Rebate/getRebateInfo",
      method: "get",
      params: params
    });
  },
  MoneyMangetgetCapitalDataInfo(params) {
    //资金首页首行数据
    return axios({
      url: "service_station/open/Capital/getCapitalDataInfo",
      method: "get",
      params: params
    });
  },
  MoneyMangetgetUnauditReceiptCount(params) {
    //资金首页未审核收款数
    return axios({
      url: "service_station/open/Capital/getUnauditReceiptCount",
      method: "get",
      params: params
    });
  },
  MoneyMangetgetReceiptRankList(params) {
    //资金首页应收和已收排行
    return axios({
      url: "service_station/open/Capital/getReceiptRankList",
      method: "get",
      params: params
    });
  },
  MoneyMangetgetGoodsList(params) {
    //获取商品信息
    return axios({
      url: "service_station/purchase/Order/getGoodsList",
      method: "get",
      params: params
    });
  },
  getClassAndBrandList(params) {
    //获取商品信息
    return axios({
      url: "service_station/purchase/Order/getClassAndBrandList",
      method: "get",
      params: params
    });
  },
  addShoppingCartGoods(params) {
    //添加购物车
    return axios({
      url: "service_station/purchase/ShoppingCart/addShoppingCartGoods",
      method: "post",
      data: params
    });
  },
  getShoppingCartGoodsCount(params) {
    //购物车数量
    return axios({
      url: "service_station/purchase/ShoppingCart/getShoppingCartGoodsCount",
      method: "get",
      params: params
    });
  },
  changeOrderPayee(data) {
    //发送汇款短信
    return axios({
      url: "service_station/purchase/Order/changeOrderPayee",
      method: "post",
      data
    });
  }
};
